@import "../../mixins";

.footerLinkWrapper {
 font-size: 12px;
 margin:7rem 0 25px 0px;

 	ul{
		padding: 0;
		text-align: center;
		display: block;
		li{
			display: inline-block;
		    margin: 0 2rem;
		}
 	}
}
.socialLinks {
  text-align: center;
}

.disclaimerText {
 margin-top: 25px;
 text-align: center;
 font-size: 16px;
}

ul {
 display: -ms-flexbox;
 display: flex;
 -ms-flex-pack: justify;
     justify-content: space-between;
 list-style-type: none;
 font-size: 2em;
 margin: 0 auto;
}

li {
 -ms-flex-positive: 1;
     flex-grow: 1;
}

a {
 color: #fff;
 text-decoration: none;
 -webkit-transition: all .5s ease-in-out;
 -o-transition: all .5s ease-in-out;
 transition: all .5s ease-in-out;
}
[title="Twitter"]:hover{
 color: rgb(85,172,238);
}
[title="Instagram"]:hover{
 color: rgb(124,86,65);
}
[title="Envelope"]:hover{
 color: rgb(0,119,181);
}
[title="Facebook"]:hover{
 color: rgb(178,36,52);
}
[title="Telegram"]:hover{
 color: rgb(230,182,10);
}
[center]{
 text-align: center;
}

@include breakpoint(tablet) {
 ul {
  width: 60%;
 }
}

@include breakpoint(laptop) {
 ul {
  width: 40%;
 }
}


