@import "settings"; 

:root { font-size: 10px; }
html{
  font-size: 1.0rem;
    height: 100%;

    body{
      height: 100%;
      color: white;
      @include quickSand;
      font-size: 2.5rem;
    }
}

#particle_wrapper{
  position: fixed;
  width: 100%;
  height: 100%;
  pointer-events: none;
  &:after{
    content: '';
    width: 100%;
    height: 100%;
    background-image: url('./images/one_pager.jpg');
    top:0;
    left: 0;    
    position: absolute;
    opacity: 0.4;
  }
}

h1,h2,h3,h4{
  @include quickSand;
  font-size: 4.2rem;
  font-weight: 700;
}

.negative_margin{
  margin: 0 -15px;
}

.draw_content_container{
  width: 100%;
  max-width: 97.9rem;
  margin: 0 auto;
  padding: 40px 5px 25px 5px;
}

.custom-button{
  @include btn;
}

span.large_text{
  font-size: 3rem;
  font-weight: 700;
  margin: 0 0.7rem;
}


// responsive
// tablet
@media screen and (max-width: 1060px) and (min-width: 841px){
  header {
    margin-bottom: 0rem;
  }
}

// mobile
@media only screen and (max-width: 1020px) {
  #logo{
    img{
      max-width: 17rem;
    }
  }

  header{
    margin-bottom: 0;
    padding-top: 20px;
  }

  .headerText{
    margin-bottom: 2rem;
    font-size: 3.2rem;
  }

  .footerLinkWrapper{
    margin-top: 4rem;
  }
 }




